import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

const localeKeys = ['en', 'zh-CN', 'Korean', 'Russian', 'Japanese']

const messages = {}
for (const key of localeKeys) {
	console.log(key)
	messages[key] = require(`./locales/${key}/index.js`).default
}

export default new VueI18n({
	locale: localStorage.getItem('languageConverter'),
	messages,
	silentTranslationWarn: true 
})