<script>
export default {
    data() {
        return {
            items: [
                { title: 'Where can I buy PCD (Phecda Token)?', content: 'You can purchase PCD (Phecda Token) on pancakeswap and ave.ai.', showContent: true },
                { title: 'Where to find more information about PhecdaChain?', content: 'We will soon launch a user manual to help you understand the principles and methods.', showContent: false },
                { title: 'Is PCD listed on centralized exchanges?', content: 'Yes, we will list it on more exchanges in the future', showContent: false },
                { title: 'Where to find PhecdaChain Roadmap?', content: 'In the documentation we are about to release, there will be very detailed plans and roadmaps', showContent: false },
                { title: 'Is the PhecdaChain Blockchain Mainnet operational?', content: 'Expected to be operational in the second half of 2024', showContent: false },
                { title: 'Where to find more information about PCD?', content: 'Please continue to follow us on Twitter', showContent: false },
                { title: 'The PSD Bridge by PhecdaChain is slated for launch in the fourth quarter of 2024.', content: 'Yes, PSD is a great cross-chain bridge design, safe, fast and without authorization.', showContent: false },
            ]
        }
    },
    mounted() {
        this.bigImg();
    },
    methods: {
        bigImg() {
            this.scrollHandler = () => {
                let rotateImg = this.$refs.rotateImg;
                let element = this.$refs.element;
                if (!rotateImg || !element) {
                    return;
                }
                let elementRect = element.getBoundingClientRect();
                let viewportHeight = window.innerHeight;
                let triggerPosition = elementRect.top - viewportHeight + 1000;

                if (window.scrollY >= triggerPosition) {
                    let scrollAmount = window.scrollY - triggerPosition;
                    let scaleAmount = Math.min((scrollAmount / 2) / viewportHeight, 1);
                    let scaleValue = scaleAmount.toFixed(8);

                    element.style.opacity = scaleValue;
                    element.style.transform = `perspective(1200px) scale(${scaleValue}) rotateX(${scaleValue}deg)`;
                } else {
                    element.style.transform = 'perspective(1200px) scale(0) rotateX(0)';
                }
            };
            window.addEventListener('scroll', this.scrollHandler);
        },
        toggleContent(index) {
            this.items[index].showContent = !this.items[index].showContent;
        }
    },
    beforeDestroy() {
        window.removeEventListener('scroll', this.scrollHandler);
    }
};


</script>

