import Vue from 'vue';
import VueRouter from 'vue-router';
import AirDrop from './view/AirDrop.vue';
import Index from './view/Index.vue';
Vue.use(VueRouter);

const

	routes = [{
		path: '/',
		component: Index,
	},
	{
		path: '/airdrop',
		component: AirDrop,
	},
	];

const router = new VueRouter({
	routes
});

export default router;